import React from 'react';
import { StaticQuery, graphql, Link } from 'gatsby';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { Header, BlogList, ContentNav } from 'components';
import { Layout, Container } from 'layouts';
import config from '../../config/site';

const Archives = ({ pageContext, data }) => {
  const { spath, archive } = pageContext;
  const posts = data.allMarkdownRemark.edges;
  const archives = [];
  posts.forEach(({ node }) => {
    if (node.frontmatter.date) {
      var dates = node.frontmatter.date.split('-');
      if (!archives[dates[0]+'-'+dates[1]]) {
        archives[dates[0]+'-'+dates[1]] = [];
      }
      archives[dates[0]+'-'+dates[1]].push(node);
    }
  });
  const list = archives[archive];
  return (
    <Layout>
      <Helmet title={`文章 | ${config.siteTitle}`} />
      <Header title={`${config.siteTitle}`}></Header>
      <Container>
        <ContentNav path="blog" title="文章" keyword="全部文章"></ContentNav>
        {list.forEach(({ node }) => (
          <BlogList
            key={node.id}
            cover={node.frontmatter.cover.childImageSharp.fluid}
            stype={node.frontmatter.type}
            path={node.frontmatter.path}
            title={node.frontmatter.title}
            date={node.frontmatter.date}
            tags={node.frontmatter.tags}
            excerpt={node.excerpt}
          />
        ))}
      </Container>
    </Layout>
  );
};

export default props => (
  <StaticQuery
    query={graphql`
    query {
      allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
        edges {
          node {
            id
            excerpt(pruneLength: 200)
            frontmatter {
              title
              path
              tags
              type
              categores
              date(formatString: "YYYY-MM-DD")
              cover {
                childImageSharp {
                  fluid(
                    maxWidth: 1000
                    quality: 90
                    traceSVG: { color: "#2B2B2F" }
                  ) {
                    ...GatsbyImageSharpFluid_withWebp_tracedSVG
                  }
                }
              }
            }
          }
        }
      }
    }
  `}
  render={data => <Archives data={data} {...props} />}
/>
)

Archives.propTypes = {
  pageContext: PropTypes.shape({
    spath: PropTypes.string.isRequired,
    archive: PropTypes.string.isRequired,
  }),
  data: PropTypes.object.isRequired,
};
